const mapStateToProps = state => {
  return {
    phaseResults: state.phaseResults,
    selectedResult: state.selectedResult,
    globeStatus: state.globeStatus
    // sections: state.sections,
    // apscData: filterStatsByYear(state.apscData, state.selectedYear, ['classification', 'gender', 'age']),
    // selectedYear: state.selectedYear,
    // availableYears: getAvailableYears(state.apscData)
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchApscData: () => {
      dispatch(fetchApscData());
    },
    onYearChange: selectedYear => {
      dispatch(selectYear(selectedYear));
    },
    onSelectResult: result => {
      dispatch(selectResult(result))
    },
    setGlobeStatus: newStatus => {
      dispatch(setGlobeStatus(newStatus))
    }
  };
};

const AppContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(App);

export default AppContainer;
