import { SET_GLOBE_STATUS } from '../constants/ActionTypes'

const initialState = {
  spinning: true
}

const globeStatus = (state = initialState, action) => {
  switch (action.type) {
    case SET_GLOBE_STATUS: {
      return Object.assign({}, state, action.newStatus)
    }
    default:
      return state
  }
};

export default globeStatus
