import React, { Component } from 'react'
import PropTypes from 'prop-types'
import AthleteCard from './AthleteCard'
import AthleteDetailCard from './AthleteDetailCard'
import './AthleteList.scss'

class AthleteList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      viewDetails: false
    }
  }

  onClickAthleteCard(result) {
    const { onSelectResult, setGlobeStatus } = this.props
    this.setState({ viewDetails: true })
    onSelectResult(result)
    setGlobeStatus({ spinning: false })
  }

  onCloseDetail() {
    const { setGlobeStatus } = this.props
    this.setState({ viewDetails: false })
    setGlobeStatus({ spinning: true })
  }

  render() {
    const _this = this
    const { phaseResults, selectedResult } = this.props
    const athleteCards = phaseResults.map( r => <AthleteCard key={r.n_PhaseResultID} result={r} onClick={() => _this.onClickAthleteCard(r)} /> )
    const heading = phaseResults.length > 0 ? `${phaseResults.length} results` : 'Loading ...'

    const cssClass = this.state.viewDetails ? 'athlete--list--container athlete--list--container__detailed' : 'athlete--list--container'
    return (
      <div className={cssClass}>
        <div className="athlete--list p-3">
          <h3 className="my-3">{heading}</h3>
          {athleteCards}
        </div>
        <AthleteDetailCard selectedResult={selectedResult} onClick={() => _this.onCloseDetail()} />
      </div>
    );
  }
}

AthleteList.propTypes = {
  phaseResults: PropTypes.array.isRequired
}

export default AthleteList