import React, { Component } from 'react'
import {format, parse} from 'date-fns'
import './AthleteDetailCard.scss'

class AthleteDetailCard extends Component {
  constructor(props) {
    super(props)
    this.state = {  }
  }
  render() {
    const { selectedResult, onClick } = this.props
    const formattedDate = format(parse(selectedResult.d_PhaseDate), 'D MMMM YYYY')
    const name = selectedResult.c_Person === null ? `Team ${selectedResult.c_Team}` : selectedResult.c_Person
    const medal = selectedResult.n_RankMedal === 1 ? "ðŸ¥‡" : (selectedResult.n_RankMedal === 2 ? "ðŸ¥ˆ" : (selectedResult.n_RankMedal === 3 ? "ðŸ¥‰" : selectedResult.n_RankMedal))
    const result = selectedResult.c_Result !== null ? (<div><dt>Result ({selectedResult.c_ResultType})</dt><dd>{selectedResult.c_Result}</dd></div>) : null
    return (
      <div className="athlete--detail--card media rounded mb-3 p-3 pb-0" onClick={e => {e.preventDefault();onClick();}}>
        <div className="media-body">
          <h3 className="my-3">{name}</h3>
          <dl>
            <dt>Rank</dt>
            <dd class="display-4">{medal}</dd>
            <dt>Sport</dt>
            <dd>{selectedResult.c_EventGender} { selectedResult.c_Sportname}</dd>
            <dt>Date</dt>
            <dd>{formattedDate} {selectedResult.n_PersonAgeYears ? `(${selectedResult.n_PersonAgeYears} years old)` : ''}</dd>
            <dt>Competition</dt>
            <dd>{selectedResult.c_CompetitionName}</dd>
            <dt>Location</dt>
            <dd>{selectedResult.c_PhaseCity} {selectedResult.c_PhaseCountry}</dd>
            {result}
          </dl>
        </div>
      </div>
    )
  }
}

export default AthleteDetailCard