import React, { Component } from 'react'
import Globe from './Globe/Globe'
import AthleteList from './AthleteList/AthleteList'
import '../vendors/stylesheets/bootstrap/bootstrap.scss'
import './App.scss'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loaded: false
    }
  }

  componentDidMount() {
    if (this.state.loaded === false) {
      this.props.fetchApscData()
      this.setState({ loaded: true })
    }
  }

  render() {
    const { phaseResults, selectedResult, onSelectResult, globeStatus, setGlobeStatus } = this.props
    return (
      <div className="container-fluid p-0">
        <div className="row no-gutters">
          <div className="col-sm-9 col-lg-10">
            <Globe phaseResults={phaseResults} selectedResult={selectedResult} globeStatus={globeStatus} />
          </div>
          <div className="col-sm-3 col-lg-2">
            <AthleteList phaseResults={phaseResults} selectedResult={selectedResult} onSelectResult={onSelectResult} setGlobeStatus={setGlobeStatus}/>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
