import fetch from 'isomorphic-fetch'
import * as types from '../constants/ActionTypes'
import countries from '../assets/data/countries.json'
// import cities from 'cities.json'

export const searchTerm = (text) => ({
  type: types.SEARCH_TERM,
  text
})

export const showSection = (sectionName) => ({
  type: types.SHOW_SECTION,
  sectionName
})

export const requestApscData = () => ({
  type: types.REQUEST_APSC_DATA,
})

export const receiveApscData = (json) => ({
  type: types.RECEIVE_APSC_DATA,
  json
})

export const fetchApscData = () => {
  const requestUrl = `/build/assets/phaseresults.json`;
  return dispatch => {
    dispatch(requestApscData());
    return fetch(requestUrl)
      .then(response => {
        return response.json()
      })
      .then(json => {
        const results = [];
        if (json.length > 5) {
          const subset = json.slice(0, 20)
          subset.forEach(r => {
            const geojson = countries.find(c => c.Country === r.c_PhaseCountry)
            results.push(Object.assign({}, r, { geojson: geojson }));
          })
        }
        dispatch(receiveApscData(results))
      })
  }
}

export const selectYear = selectedYear => ({
  type: types.SELECT_YEAR,
  selectedYear
})

export const selectResult = selectedResult => ({
  type: types.SELECT_RESULT,
  selectedResult
})

export const setGlobeStatus = newStatus => ({
  type: types.SET_GLOBE_STATUS,
  newStatus
})
