import React, { Component } from 'react'
import MyGlobe from './MyGlobe'
import './Globe.scss'

class Globe extends Component {
  constructor(props) {
    super(props);
    this.myGlobe = new MyGlobe()
  }

  render() {
    return (
      <div>
        <div id="my-globe"></div>
      </div>
    );
  }

  componentDidUpdate() {
    const { phaseResults, selectedResult, globeStatus } = this.props
    this.myGlobe.updatePhaseResults(phaseResults)
    if (globeStatus.spinning === false) {
      this.myGlobe.rotateCamera(selectedResult)
    }
    this.myGlobe.applyStatus(globeStatus)
  }

  componentDidMount() {
    this.myGlobe.init()
  }
}

export default Globe;