import React, { Component } from 'react'
import {format, parse} from 'date-fns'
import './AthleteCard.scss'

class AthleteCard extends Component {
  render() {
    const { result, onClick } = this.props
    const formattedDate = format(parse(result.d_PhaseDate), 'D MMMM YYYY')
    const name = result.c_Person === null ? `Team ${result.c_Team}` : result.c_Person
    const medal = result.n_RankMedal === 1 ? "ðŸ¥‡" : (result.n_RankMedal === 2 ? "ðŸ¥ˆ" : (result.n_RankMedal === 3 ? "ðŸ¥‰" : result.n_RankMedal))
    // const lng = result.geojson ? result.geojson.lng : ""
    // const lat = result.geojson ? result.geojson.lat : ""
    return (
      <div className="athlete--card media rounded mb-3 p-2 pb-0" onClick={e => {e.preventDefault();onClick();}}>
        <div className="media-body">
          <h5 className="mt-0">{medal} {name}</h5>
          <p class="mb-0">
            {result.c_EventGender} { result.c_Sportname}, {result.c_CompetitionName}<br/>
            <small class="text-muted">
              {result.c_PhaseCity} {result.c_PhaseCountry}<br/>
              {formattedDate}<br/>
            </small>
          </p>
        </div>
      </div>
    )
  }
}

export default AthleteCard;